
.left-enter{
	 -webkit-transform: translateX(100%);
	         transform: translateX(100%);
}
.left-enter-active,
.left-leave-active,
.right-enter-active,
.right-leave-active
{
	-webkit-transition: .4s ease-in-out;
	transition: .4s ease-in-out;
}
.left-leave-to{
	-webkit-transform: translateX(-100%);
	        transform: translateX(-100%);
}
.right-enter{
	 -webkit-transform: translateX(-100%);
	         transform: translateX(-100%);
}
.right-leave-to{
	-webkit-transform: translateX(100%);
	        transform: translateX(100%);
}




